body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.34;
  font-family: "PT Mono", "Courier New", monospace; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

input,
textarea,
select,
button {
  font: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff") format("woff"), url("../fonts/PTMono-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

.container {
  max-width: 1440px;
  min-width: 1024px;
  margin: 0 auto; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1023px;
      min-width: 768px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 767px;
      min-width: 320px; } }

.header {
  height: 881px;
  background-color: #1b2d37;
  background: url("../img/promo-bg.png") no-repeat right center, url("../img/background.jpg") repeat-x; }
  @media (-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .header {
      background: url("../img/promo-bg@2x.png") no-repeat right center, url("../img/background@2x.jpg") repeat-x; } }
  @media (max-width: 1023px) {
    .header {
      height: 688px;
      background-size: 465px 340px, auto;
      background-position: 170% 40%, 0 0; } }

.header__wrapper {
  padding-top: 33px;
  padding-left: 10px;
  padding-right: 10px; }
  @media (max-width: 1023px) {
    .header__wrapper {
      padding-top: 22px;
      padding-left: 25px;
      padding-right: 0px; } }
  @media (max-width: 767px) {
    .header__wrapper {
      padding-top: 18px;
      padding-left: 15px;
      padding-right: 10px; } }

.main-nav {
  display: flex;
  padding-left: 2px;
  padding-right: 6px; }

.main-nav__logo {
  margin-right: auto; }
  .main-nav__logo a {
    display: flex;
    align-items: center; }

.main-nav__logo-icon {
  margin-right: 16px; }
  @media (max-width: 767px) {
    .main-nav__logo-icon {
      width: 38px;
      height: 38px; } }

.main-nav__slogan {
  margin-top: 2px;
  fill: #ffffff;
  color: #788891; }
  @media (max-width: 767px) {
    .main-nav__slogan {
      display: none; } }

.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  min-width: 660px; }
  @media (max-width: 1023px) {
    .main-nav__list {
      min-width: auto; } }

.main-nav__item {
  margin-right: 88px;
  cursor: pointer;
  display: flex;
  align-items: center; }
  .main-nav__item a {
    color: #ffffff;
    display: flex;
    align-items: center; }
  .main-nav__item:nth-child(2n) {
    margin-right: 50px; }
  .main-nav__item:last-child {
    margin: 0; }
    .main-nav__item:last-child a {
      font-family: "Roboto Condensed", "Helvetica", sans-serif;
      font-size: 14px;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 5px;
      border: 1px solid #ffffff;
      padding: 12px 27px 13px 26px; }
    @media (max-width: 1023px) {
      .main-nav__item:last-child {
        display: none; } }
  @media (max-width: 1023px) {
    .main-nav__item {
      margin-right: 15px;
      height: 49px; }
      .main-nav__item:nth-child(2n) {
        margin-right: 0; }
      .main-nav__item span {
        display: none; } }
  @media (max-width: 767px) {
    .main-nav__item {
      margin-right: 23px;
      height: 33px; } }

.main-nav__icon {
  margin-right: 20px;
  margin-top: -1px; }
  .main-nav__icon--phone {
    margin-right: 16px; }
  @media (max-width: 1023px) {
    .main-nav__icon {
      width: 25.3px;
      height: 19px; }
      .main-nav__icon--phone {
        width: 24px;
        height: 24px; } }
  @media (max-width: 767px) {
    .main-nav__icon {
      margin-right: 0; } }

.promo {
  padding-top: 175px; }
  @media (max-width: 1023px) {
    .promo {
      padding-top: 75px;
      padding-left: 65px; } }
  @media (max-width: 767px) {
    .promo {
      padding-left: 0;
      padding-top: 203px; } }
  .promo h1 {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    font-size: 80px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    line-height: 94px;
    color: #ffffff;
    margin-bottom: 27px; }
    @media (max-width: 1023px) {
      .promo h1 {
        font-size: 60px;
        line-height: 70px;
        letter-spacing: 0.07em;
        max-width: 44%;
        margin-bottom: 29px; } }
    @media (max-width: 767px) {
      .promo h1 {
        font-size: 40px;
        line-height: 47px;
        margin-bottom: 27px; } }
  .promo a {
    display: inline-block;
    color: #ffffff;
    font-size: 14px;
    line-height: 16px; }
  .promo > a {
    transform: rotate(-90deg) translate(-128px, -35px);
    opacity: 0.5; }
    @media (max-width: 1023px) {
      .promo > a {
        display: none; } }

.promo__info {
  padding-left: 96px;
  max-width: 720px; }
  @media (max-width: 1023px) {
    .promo__info {
      max-width: 70%;
      padding-left: 52px; } }
  @media (max-width: 767px) {
    .promo__info {
      max-width: 100%;
      padding: 0; } }
  .promo__info p {
    color: #ffffff;
    font-size: 22px;
    position: relative;
    line-height: 34.5px;
    margin: 0;
    margin-bottom: 44px; }
    .promo__info p::before {
      content: "";
      width: 66px;
      height: 2px;
      background-color: #ffffff;
      position: absolute;
      top: 17px;
      left: -93px; }
    @media (max-width: 1023px) {
      .promo__info p {
        font-size: 18px;
        line-height: 28px;
        margin-bottom: 54px; }
        .promo__info p::before {
          width: 33px;
          top: 15px;
          left: -48px; } }
    @media (max-width: 767px) {
      .promo__info p {
        padding-left: 52px;
        font-size: 16px;
        line-height: 25px;
        margin-bottom: 40px; }
        .promo__info p::before {
          top: 15px;
          left: 3px; } }
  .promo__info a {
    font-family: "Roboto Condensed", "Helvetica", sans-serif;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-radius: 5px;
    font-size: 16px;
    background-color: #00b2ff;
    padding: 19px 45px 20px 45px; }
    .promo__info a span:last-child {
      display: none; }
    @media (max-width: 1023px) {
      .promo__info a {
        padding: 19px 31px 20px 31px; } }
    @media (max-width: 767px) {
      .promo__info a {
        display: block;
        margin: 0 auto;
        width: 287px;
        padding: 19px 25px 20px 30px; }
        .promo__info a span:last-child {
          display: block; }
        .promo__info a span:first-child {
          display: none; } }

.footer {
  background-color: #fafafa;
  padding-top: 50px; }

.footer__grid {
  display: grid;
  grid-template-rows: 214px auto;
  grid-template-columns: 400px auto; }

.footer__header {
  grid-row: 1;
  grid-column: 1;
  padding-top: 15px;
  padding-left: 11px; }

.footer__main {
  grid-row: 1;
  grid-column: 2 / 3;
  display: flex;
  justify-content: space-between;
  padding-left: 32px;
  padding-right: 16px; }

.footer__footer {
  grid-row: 2;
  grid-column: 1 / 3;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  padding-right: 16px; }

.footer__logo {
  display: flex;
  margin-bottom: 48px; }

.footer__logo-icon {
  margin-right: 6px; }

.footer__slogan {
  fill: #203c42;
  color: #788891; }

.socials__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex; }

.socials__item:not(:last-child) {
  margin-right: 17px; }

.copyright {
  display: flex;
  justify-content: space-between;
  min-width: 457px; }
  .copyright p {
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #1b2d37;
    opacity: 0.5;
    margin-top: 15px; }

.site-section h3 {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  text-transform: uppercase;
  color: #1b2d37;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  margin-bottom: 25px;
  margin-left: 3px; }

.site-section__list {
  list-style: none;
  margin: 0;
