  flex-direction: column;
  flex-wrap: wrap;
  max-height: 121px;
  min-width: 500px; }

.site-section__item a {
  color: #1b2d37;
  font-size: 15px;
  line-height: 25px;
  opacity: 0.5; }

.office h3 {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  text-transform: uppercase;
  color: #1b2d37;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  margin-bottom: 25px; }

.office p {
  color: #1b2d37;
  font-size: 15px;
  line-height: 25px;
  opacity: 0.5;
  margin: 0; }

.feedback {
